import request from '@/utils/request'
export function bannerList() {
    return request({
        url: 'api/index/banner',
        method: 'get'
    })
}
// 绑定手机号
export function bindPhone(phone) {
    return request({
        url: 'api/login/bindPhone',
        method: 'post',
        data:{phone}
    })
}
// 广告图
export function advertising() {
    return request({
        url: 'api/index/advertising',
        method: 'get'
    })
}
export function showTicked() {
    return request({
        url: 'api/index/showTicked',
        method: 'get'
    })
}
export function categoryList() {
    return request({
        url: 'api/index/categoryList',
        method: 'get'
    })
}
export function clickTicked(id) {
    return request({
        url: 'api/index/clickTicked',
        method: 'get',
        params:{id}
    })
}
export function indexgoodsList(category_id,goods_name) {
    return request({
        url: 'api/index/index',
        method: 'get',
        params:{category_id,goods_name}
    })
}
