<template>
  <div>
    <van-sticky>
      <van-nav-bar title="商品详情" left-arrow @click-left="onClickLeft" />
    </van-sticky>
    <!-- 商品主图 -->

  <van-swipe :autoplay="3000">
      <van-swipe-item v-for="image in slides" :key="image.index">
        <img :src="image" />
      </van-swipe-item>
    </van-swipe>
    <!-- 介绍 -->
    <div class="detailsMid" @click="goPay">
      <div class="goodsName">
        {{ goodsInfo.goods_name }}
      </div>
      <div class="goodsCon">
        <span class="nowPrice">￥{{ goodsInfo.price }}</span>
        <!-- <span class="oldPrice">原价:{{ goodsInfo.yprice }}</span> -->
        <!-- <span class="rightEl">销量:{{ goodsInfo.sales }}</span> -->
      </div>
        <div class="goodsConr">
        <!-- <span class="oldPricer">运费:{{ goodsInfo.freight }}</span> -->
        <span class="oldPricer">库存:{{ goodsInfo.inventory }}</span>
        <span class="oldPricer">销量:{{ goodsInfo.sales }}</span>
        <!-- <span class="oldPricer">地区:{{ goodsInfo.yprice }}</span> -->
        <!-- <span class="rightEl">销量:{{ goodsInfo.sales }}</span> -->
      </div>
      <!-- <div class="freight">
        满<span>{{ freight.nofree }}元</span>免运费
      </div> -->
    </div>
    <!-- <div class="reduceList" v-if="reduceList.length > 0">
      <div v-for="item in reduceList" class="reduceItem">
        满{{ item.money }}元减{{item.dec}}元
      </div>
    </div> -->
    <!-- <van-cell title="选择规格" is-link @click="chooseShow" /> -->
    <div class="gap"></div>
    <div class="detailsDesc">
      <p class="descTitle">宝贝详情</p>
      <div v-html="goodsInfo.detail"></div>
    </div>
    <!-- 规格选择 -->
    <van-popup
      v-model="show"
      position="bottom"
      :style="{ height: '58%' }"
      round
    >
      <div class="popWrap">
        <div class="popTop">
          <img :src="goodsInfo.imgs" alt="" class="popImg leftEl" />
          <div class="leftEl">
            <div class="price popNow">￥{{ goodsInfo.price }}</div>
            <div class="price popOld">原价:￥{{ goodsInfo.yprice }}</div>
            <div class="price">
              已选<span>{{ chooseSize.remark }}</span>
            </div>
          </div>
        </div>
        <div class="popMid">
          <p class="midTitle">规格</p>
          <div class="chooseWrap">
            <div
              class="chooseItem"
              v-for="(item, index) in goodsSize"
              :key="index"
              :class="index == activeIndex ? 'choosedSize' : ''"
              @click="getSize(item, index)"
            >
              {{ item.remark }}
            </div>
            <div class="chooseItem blankItem"></div>
          </div>
        </div>
        <div class="popNum">
          <span>购买数量</span>
          <van-stepper v-model="goodsValue" class="rightEl" />
        </div>
        <div class="popBtn showBtn">
          <button class="addBtn" @click="toShopCar">加入购物车</button>
          <button class="buyBtn" @click="goPay">立即购买</button>
        </div>
      </div>
    </van-popup>
    <!-- 按钮 -->
    <div class="popBtn">
      <button class="buyBtn" @click="goShowPay(goodsInfo)">立即购买</button>
    </div>
  </div>
</template>

<script>
import { Lazyload } from "vant";
import {
  goodsDetail,
  addCart,
  showFreight,
  goodsSize,
  nowBuy,
  reduceList
} from "@/api/mall";
export default {
  data() {
    return {
      activeIndex: 0, //规格默认
      slides: [],
      show: false,
      goodsValue: 1,
      goodsId: "",
      goodsInfo: {},
      freight: "",
      goodsSize: [],
      chooseSize: {},
      reduceList: [] //满减列表
    };
  },
  created() {
    this.$toast.loading({
      mask: true,
      duration: 500,
      message: "加载中..."
    });
    this.$toast.clear();
    this.goodsId =   JSON.parse(localStorage.getItem("goodsId"));
    this.goodsInfo = this.goodsId ;
    this.slides=this.goodsInfo.imgs.split(',')
  },
  methods: {
    toShowShopCar() {
      this.show = true;
    },
    goShowPay(id) {
        
          this.$router.push({
        path: "payOrder",
        query: {
          id: id,
        }
      });
    },
    // 选择规格
    getSize(item, index) {
      this.activeIndex = index;
      this.chooseSize = item;
    },
    onClickLeft() {
      this.$router.push("mall");
    },
    chooseShow() {
      this.show = true;
    },
    // 购物车
    toShopCar() {
      let data = {
        goods_id: this.goodsInfo.id,
        nums: this.goodsValue,
        size_id: this.chooseSize.id,
        size_name: this.chooseSize.remark
      };
      addCart(data).then(res => {
        this.$toast("成功加入购物车!");
        this.show = false;
      });
    },
    // 立即购买
    goPay() {
      this.$router.push({
        path: "payOrder",
        query: {
          type: 1,
        }
      });
    }
  }
};
</script>
<style lang="less" scoped>
.van-swipe {
  height: 301px;
  img {
    width: 100%;
    height: 100%;
  }
}
.detailsMid {
  margin: 13.5px 13px 0;
  padding-bottom: 10px;
  border-bottom: 1px solid #dcdcdc;
}
.reduceList{
  display: flex;
  width:100%;
  justify-content:space-between;
  flex-wrap: wrap;
  padding:13px 13px 3px;
  box-sizing:border-box;
  border-bottom: 1px solid #dcdcdc;
}
.reduceItem {
  background: #f5f5f5;
  border-radius: 5px;
  font-size: 13px;
  color: #de4f53;
   padding:0 10px;
  margin-bottom:10px;
  text-align: center;
  height:20px;
  line-height: 20px;
}
.goodsName {
  color: #333;
  font-size: 15px;
  font-weight: bold;
  margin-bottom: 16px;
}
.goodsCon {
  color: #666;
  font-size: 12px;
}
.goodsConr {
  color: #666;
  font-size: 12px;
  display: flex;
  margin-top: 5px;
  
}
.nowPrice {
  color: #de4f53;
  font-size: 18px;
  margin-right: 10px;
}
.oldPrice {
  text-decoration: line-through;
}
.oldPricer {
    flex: 1;
}
.oldPricer:nth-child(1) {
    flex: 1;
    color: #333;
    font-size: 14px;
}
.gap {
  width: 100%;
  height: 10px;
  background: #f6f6f6;
}
.descTitle {
  color: #333;
  font-size: 17px;
  text-align: center;
  margin: 15px auto;
}
.freight {
  margin-top: 7px;
  font-size: 15px;
  span {
    color: #de4f53;
  }
}
.van-popup {
  font-size: 13px;
  color: #999;
}
.popWrap {
  padding: 14.5px 13.5px 5px 16px;
  width: 100%;
  box-sizing: border-box;
}
.popTop {
  padding-bottom: 14.5px;
  overflow: hidden;
  border-bottom: 1px solid #d2d2d2;
}
.popImg {
  width: 70.5px;
  height: 70.5px;
  margin-right: 13px;
  border-radius: 6px;
}
.popNow {
  color: #de4f53;
  font-size: 15px;
}
.popOld {
  margin: 13.5px 0 12px;
  text-decoration: line-through;
}
.popMid {
  padding: 5px 0 12.5px;
  border-bottom: 1px solid #d2d2d2;
}
.midTitle {
  color: #333;
}
.chooseWrap {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.chooseItem {
  border: 1px solid #999;
  padding: 3.5px;
  width: 25%;
  text-align: center;
  border-radius: 2px;
  margin-bottom: 10px;
  margin-right: 20px;
}
.chooseItem:nth-of-type(3n) {
  margin-right: 0;
}
.blankItem {
  border: 0 none;
}
.choosedSize {
  background: #ffbbbb;
  border: 0 none;
  color: #ff0101;
}
.popNum {
  padding-top: 16.5px;
  color: #333;
  margin-bottom: 18px;
}
.popBtn {
  width: 100%;
  height: 50px;
  position: fixed;
  padding-top: 11px;
  left: 0;
  bottom: 0;
  text-align: center;
  z-index: 1000;
  button {
    width: 300.5px;
    height: 38px;
    border: 0 none;
    font-size: 18px;
  }
}

.buyBtn {
  background: #28A6FF;
  color: #fff;
  border-radius: 20px;
}
.detailsDesc {
  font-size: 15px;
  padding-bottom: 60px;
  /deep/ img {
    width: 100%;
    height: auto;
    display: block;
  }
}
.showBtn {
  position: relative;
}
</style>
